import DashboardLayout from "components/layouts/DashboardLayout";
import { useEffect, useState } from "react";
import { navigate, Link } from "gatsby";
import { format } from "date-fns";
import { ru } from "date-fns/locale";
import { LinkPrevious } from "grommet-icons";
import { DASHBOARD_SCHEDULE } from "src/routes";
import styled from "styled-components";
import { Select, Grid, Box, TextArea, Button } from "grommet";
import { Container } from "components/common/Container";
import {
  useFunctions,
  withFirebaseProvider,
} from "components/FirebaseProvider";
import to from "await-to-js";
import { useParams } from "@reach/router"

const ATTEND_INTERVIEW = "interview";
const ATTEND_PRACTICE = "practice";
const ATTEND_CONSULTATION = "consultation";

const ATTEND_TYPES = [
  {
    label: "[FRONT-END] Interview",
    value: ATTEND_INTERVIEW,
    placeholder:
      "Привет, напиши сюда свои контактные данные и пару слов о себе",
  },
  {
    label: "[FRONT-END] Практика",
    value: ATTEND_PRACTICE,
    placeholder:
      "Добро пожаловать, снова! Напиши сюда свои вопросы по интересующему уроку",
  },
  {
    label: "[ANY] Консультация",
    value: ATTEND_CONSULTATION,
    placeholder:
      "Привет, чужестранник! Я могу проконсультировать тебя по HTML, JS, react и node.js. А так же по backend сервисам google, stripe, twilio и тд. Еще активно занимаюсь видеозаписью и живыми трансляциями. Интересуют любые предложения по сотрудничеству и активно ищу преподователей для Hipsta Conding School.",
  },
];

const HOUR = 1 * 60 * 60 * 1000;
const HALF_HOUR = 30 * 60 * 1000;

const ATTEND_TIME = {
  [ATTEND_INTERVIEW]: [{ value: HALF_HOUR, label: "пол часика" }],
  [ATTEND_PRACTICE]: [{ value: HOUR, label: "часик" }],
  [ATTEND_CONSULTATION]: [
    { value: HALF_HOUR, label: "пол часика" },
    { value: HOUR, label: "часик" },
  ],
};

const BackLink = styled(Link)`
  color: ${({ theme }) => theme.colors.textColor};
  opacity: 0.3;
  transition: all 0.3s;
  display: flex;
  align-items: center;
  font-size: 20px;
  margin-bottom: 10px;

  &:hover {
    opacity: 1;
  }

  svg {
    transition: all 0.3s;
    margin-right: 10px;
  }

  &:hover svg {
    fill: ${({ theme }) => theme.colors.textSecondaryColor};
    stroke: ${({ theme }) => theme.colors.textSecondaryColor};
  }
`;

const PageTitle = styled.h1`
  font-size: 36px;
`;

const Label = styled.label`
  font-size: 28px;
  margin-bottom: 20px;
`;

const LessonCreatePage = ({ location }) => {
  const DEFAULT_ATTEND_TYPE = ATTEND_TYPES[0];
  const [type, setType] = useState(DEFAULT_ATTEND_TYPE);
  const currentAttendTime = ATTEND_TIME[type.value];
  const params = useParams()
  const date = new Date(params.date);
  const [time, setTime] = useState(currentAttendTime[0]);
  const [details, setDetails] = useState(null);
  const functions = useFunctions();

  useEffect(() => {
    setTime(currentAttendTime[0]);
  }, [type]);

  const createEvent = async e => {
    const takeLessonFirebaseFn = functions.httpsCallable("takeLesson");
    const [err] = await to(
      takeLessonFirebaseFn({
        eventName: type.label,
        description: details,
        startTime: date.toISOString(),
      })
    );

    if (err) {
      return console.error(err);
    }

    navigate(DASHBOARD_SCHEDULE);
  };

  const onSubmit = e => {
    e.preventDefault();
    createEvent();
  };

  return (
      <Container>
        <BackLink to={DASHBOARD_SCHEDULE}>
          <LinkPrevious color="light-1" size="medium" /> Вернуться к выбору даты
        </BackLink>
        <PageTitle>
          Запись на{" "}
          {date ? format(date, "cccc, dd MMMM HH:mm", { locale: ru }) : null}
        </PageTitle>
        <br />
        <Grid
          as="form"
          onSubmit={onSubmit}
          rows={["auto"]}
          columns={["1/2", "1/2"]}
          gap="medium"
          areas={[
            ["type", "time"],
            ["details", "details"],
            ["subscribe", "subscribe"],
          ]}
        >
          <Box gridArea="type">
            <Label htmlFor="select-type">По поводу:</Label>
            <Select
              id="select-type"
              options={ATTEND_TYPES}
              value={type}
              labelKey="label"
              valueKey="value"
              onChange={props => {
                setType(props.option);
              }}
              dropProps={{
                elevation: "none",
              }}
            ></Select>
          </Box>
          <Box gridArea="time">
            <Label htmlFor="select-time">Длительностью:</Label>
            <Select
              id="select-time"
              disabled={currentAttendTime.length < 2}
              options={currentAttendTime}
              value={time}
              labelKey="label"
              valueKey="value"
              onChange={props => {
                setTime(props.option);
              }}
              dropProps={{
                elevation: "none",
              }}
            ></Select>
          </Box>
          <Box gridArea="details">
            <Label htmlFor="details">Детали:</Label>
            <TextArea
              id="details"
              placeholder={type.placeholder}
              resize="vertical"
              size="xlarge"
              rows={5}
              value={details}
              onChange={event => setDetails(event.target.value)}
            />
          </Box>
          <Box gridArea="subscribe" align="center">
            <Button
              disabled={!details}
              type="submit"
              primary={true}
              label="Записаться"
              size="large"
            ></Button>
          </Box>
        </Grid>
      </Container>
  );
};

export default withFirebaseProvider(LessonCreatePage);
